.input_box {
    position: relative;
    margin-bottom: 40px; padding-right: 54px;
    height: 54px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.1);
    overflow: hidden;
}
.input_box input {
    padding: 16px;
    width: 100%; height: 100%;
    font-size: 1rem;
    background: transparent;
    border-radius: 5px 0 0 5px;
}
.btn_add {
    position: absolute; top: 0; right: 0;
    width: 54px; height: 100%;
    font-size: 0;
    background: #066bb0 url(../assets/btn_add.svg) no-repeat center;
    transition: 0.3s;
}
.btn_add:hover, .btn_add:focus {
    background-color: #065c97;
}
