.todo_lists li {   
    display: flex;
    margin-bottom: 6px; padding: 10px 16px;
    min-height: 54px;
    justify-content: space-between; align-items: center;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}
.todo_txt {
    flex: 1;
}
.todo_txt input[type="checkbox"] {
    display: none;
}
.todo_txt label {
    display: block;
    padding-left: 28px; padding-bottom: 2px;
    background: url(../assets/icon_check_off.png) no-repeat left center;
}
.todo_txt input:checked + label {
    text-decoration: line-through;
    background-image: url(../assets/icon_check_on.png);
}
.todo_txt input[type="text"] {
    padding: 4px 2px 8px;
    width: 100%; 
    color: #585858; font-size: 1rem;
    border-bottom: 1px solid #ccc;
}
.todo_txt input[type="text"]:focus {
    border-color: #066bb0;
    outline: none;
}
.btn_edit, .btn_del, .btn_save {
    display: inline-block;
    margin-left: 14px;
    width: 24px; height: 24px;
    font-size: 0; vertical-align: top;
    background-color: transparent; background-repeat: no-repeat; background-position: center;
}
.btn_del {
    background-color: #f0f6fa; background-image: url(../assets/btn_del.svg); background-size: 12px;
    border-radius: 3px;
}
.btn_edit {
    background-image: url(../assets/btn_edit.svg); background-size: 20px;
}
.btn_save {
    background-image: url(../assets/btn_save.svg); background-size: 20px;
}