.todo_lists {
    margin-bottom: 40px;
    list-style-type: none; text-align: left;
}
.btn_clear {
    padding: 0 4px;
    color: #585858; font-size: 1rem; font-weight: 600; 
    background: none;
}
.btn_clear:hover::after {
    content: "";
    display: block;
    height: 1px;
    background: #585858; 
}