.app {
	margin: 0 auto; padding: 50px 0 130px;
	width: 600px; max-width: 86%; min-height: 100%;
	background-image: url(./assets/img_rect.svg), url(./assets/img_star_12angled.svg), url(./assets/img_circle.svg), url(./assets/img_triangle.svg);
	background-size: 70px, 96px, 80px, 88px;
	background-position: left 5% bottom, left 35% bottom, right 35% bottom, right 5% bottom;
	background-repeat: no-repeat;
}
.app h1 {
  margin-bottom: 50px;
  color: #585858; letter-spacing: -0.02em;
}
.app h1::before, .app h1::after {
  content: "";
  display: inline-block;
  width: 38px; height: 38px;
  vertical-align: bottom;
  background: url(./assets/img_star_6angled.svg) no-repeat center; background-size: contain;
}
.app h1::before {
  margin-right: 8px;
}
.app h1::after {
  margin-left: 8px;
}
.copyright {
  display: block;
  margin-top: 60px; 
  font-size: 13px;
}